.toaster {
  position: fixed !important;
  z-index: 10000;
  opacity: 0.9;
  width: 280px;
  right: 1em;
  margin-bottom: 1em;
  box-shadow: 0 4px 4px rgba(0, 0, 0, 0.4);
  border: 1px solid #000;
  border-radius: 4px;
  padding: 6px 1em;
  background: #eee;
  cursor: default;
  clip-path: inset(0 -4px 100% -4px round 8px);
}
.toaster .icon {
  font-size: 32px;
  line-height: 32px;
  position: relative;
  display: table-cell;
  vertical-align: middle;
  padding-right: 1em;
}
.toaster .body {
  padding: 0;
  line-height: 1.3;
  display: table-cell;
  vertical-align: middle;
}